import {
    auth,
    ref,
    database,
} from './firebase';
import createIframe from './createIframe';

window.sgliveSSO = {
    getPage(id, uid, callback) {
        const websiteRef = ref.child(`websites/${id}`);
        websiteRef
            .once('value')
            .then((snap) => {
                const val = snap.val();
                const edited = (val && val.onEdit && val.onEdit.status);
                if (edited) {
                    val.edited = true;
                } else {
                    if (val && val.displayName) {
                        this.editedId = id;
                        websiteRef
                            .update({
                                onEdit: {
                                    status: true,
                                    user: uid,
                                },
                            });
                        websiteRef
                            .onDisconnect()
                            .update({
                                onEdit: {
                                    status: false,
                                    user: uid,
                                },
                            });
                    }
                }
                callback(val);
            });
    },
    savePage(id, callback) {
        const websitesRef = ref.child(`websites`);
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    websitesRef
                        .child(id)
                        .update({
                            updatedAction: 'Edited',
                            updatedBy: auth().currentUser.uid,
                            updatedDate: database.ServerValue.TIMESTAMP,
                        })
                        .then(() => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push({
                                    updatedAction: 'Edited',
                                    updatedBy: auth().currentUser.uid,
                                    updatedDate: database.ServerValue.TIMESTAMP,
                                })
                                .then(() => {
                                    callback('edited');
                                });
                        });
                } else {
                    callback('not exists');
                }
            });
    },
    publishPage(id, callback) {
        const websitesRef = ref.child(`websites`);
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    websitesRef
                        .child(id)
                        .update({
                            updatedAction: 'Published',
                            updatedBy: auth().currentUser.uid,
                            updatedDate: database.ServerValue.TIMESTAMP,
                        })
                        .then(() => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push({
                                    updatedAction: 'Published',
                                    updatedBy: auth().currentUser.uid,
                                    updatedDate: database.ServerValue.TIMESTAMP,
                                })
                                .then(() => {
                                    callback('published');
                                });
                        });
                } else {
                    callback('not exists');
                }
            });
    },
    initializeClient(callback) {
        const contentWindow = createIframe();
        window.addEventListener('message', (event) => {
            const message = event.data;
            if (message.type === 'sglAccessToken') {
                const {
                    sglAccessToken,
                } = message.data;
                if (sglAccessToken) {
                    auth()
                        .signInWithCustomToken(sglAccessToken)
                        .then((data) => {
                            callback({
                                user: auth().currentUser,
                            });
                        })
                        .catch((error) => {
                            if (error.code === 'auth/invalid-custom-token' && auth().currentUser) {
                                callback({
                                    user: auth().currentUser,
                                });
                            } else {
                                callback({
                                    user: null,
                                });
                            }
                        });
                } else {
                    if (auth().currentUser) {
                        if (this.editedId) {
                            const websiteRef = ref.child(`websites/${this.editedId}`);
                            websiteRef
                                .update({
                                    onEdit: {
                                        status: false,
                                        user: auth().currentUser.uid,
                                    },
                                })
                                .then(() => {
                                    auth()
                                        .signOut()
                                        .then((data) => {
                                            callback({
                                                user: null,
                                            });
                                        })
                                        .catch((error) => {
                                            callback({
                                                user: null,
                                            });
                                        });
                                });;
                        } else {
                            auth()
                                .signOut()
                                .then((data) => {
                                    callback({
                                        user: null,
                                    });
                                })
                                .catch((error) => {
                                    callback({
                                        user: null,
                                    });
                                });
                        }
                    }
                }
            }
        });
    },
};



